.loginForm {
  display: flex;
  flex-direction: column;
  font-size: 1.125rem;
  padding: 0.625em;
}
.loginForm .row {
  padding: 0.625em;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loginForm .row.hidden {
  display: none;
}
.loginForm .row.buttonRow {
  padding: 0 0.625em;
}
.loginForm .row.buttonRow button:not(last-child) {
  margin-right: 0.5rem;
}
.loginForm .row .label {
  display: inline-block;
  width: 5.5em;
}
.loginForm .row .input {
  padding: 0.5em;
  font-size: 0.888em;
  border: 1px solid #9a9a9a;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  border-radius: 0.25em 0;
}
.loginForm .row .input:disabled {
  color: #9a9a9a;
  background-color: #e2e1e1;
}
.loginForm .row .formError {
  font-size: 0.777em;
  color: #c40202;
  font-weight: 600;
}
.loginForm .row .formError.hidden {
  visibility: hidden;
}
.loginForm .row .formSwitch {
  text-decoration: underline;
  font-size: 0.875em;
  border: 0;
  background: none;
}
.loginForm .row .formSwitch.disabled {
  cursor: default;
  color: #9a9a9a;
}
.loginForm .row .formSwitch:hover:not(.disabled) {
  color: #d45287;
}
