.loading-button {
  background-color: #000000;
  border: 0;
  box-sizing: border-box;
  color: #ffffff;
  transition: 0.3s color;
  font-size: 1.125rem;
  min-width: 5.5em;
  min-height: 1.9em;
  padding: 0.5em;
  margin: 0.5em 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 0.6em;
}
.loading-button:disabled {
  pointer-events: none;
  background-color: #e2e1e1;
  color: #000000;
}
.loading-button .buttonSvgLoading {
  width: 1em;
  margin: 0 0.5em 1px 0;
}
@media (orientation: landscape) and (max-height: 400px) {
  .loading-button {
    margin: 0.3em 0;
  }
}
.loading-button:hover:not(.buttonLoading) {
  background-color: #d45287;
}
.menu-button {
  background-color: #000000;
  border: 0;
  box-sizing: border-box;
  color: #ffffff;
  transition: 0.3s color;
  min-width: 5.5em;
  min-height: 1.9em;
  padding: 0.35em;
  margin: 0.5em 0;
  border-radius: 0 0.6em;
}
.menu-button:disabled {
  pointer-events: none;
  background-color: #e2e1e1;
  color: #000000;
}
@media (orientation: landscape) and (max-height: 400px) {
  .menu-button {
    margin: 0.3em 0;
  }
}
.menu-button.reversed {
  border-radius: 0.6em 0;
}
.menu-button:hover {
  background-color: #d45287;
}
.game-button {
  background-color: #000000;
  border: 0;
  box-sizing: border-box;
  color: #ffffff;
  transition: 0.3s color;
  border-radius: 2.625em;
  height: 4.1875em;
  width: 4.125em;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.game-button:disabled {
  pointer-events: none;
  background-color: #e2e1e1;
  color: #000000;
}
.game-button.small {
  height: 2.5em;
  width: 2.5em;
  font-size: 1em;
}
.game-button:hover,
.game-button.selected {
  background-color: #d45287;
}
.link-button {
  border: 0;
  padding: 0;
  margin: 0;
  color: #000000;
  font-size: 1rem;
  background-color: transparent;
}
.link-button:hover {
  color: #d45287;
  text-decoration: underline;
}
